export default {
  rem: function pl() {
//设置一个获取屏幕宽度并赋值的函数
//        获取屏幕宽度并赋值给自定义变量w
    var w = document.documentElement.offsetWidth;
    // console.log(w);
    if(320<w&&w<562){
//        把w进行计算加上px赋值给html标签的font-size
      document.documentElement.style.fontSize = w/375 * 100+ "px";
      console.log("当前rem等于",w/375 * 100,"px");
    }else if(w>=562){
      document.documentElement.style.fontSize = 150+'px';
      console.log("当前rem等于150px");
    }else if(w<=320){
      document.documentElement.style.fontSize = 86+'px';
      console.log("当前rem等于86px");
    } else {
      console.log(444);
    }
  }
}

