
import XImg from 'vux/src/components/x-img/index.vue'

import LoadMore from "../common/LoadMore";
import { homeFilterList } from "../../api/data.js"
export default {
name: "HomeFilterList",
data() {
return {



goodDataList: {},
isLoadingMore: false,
homeFilterList: homeFilterList,
paramsData: {
pageSize: 20,
param_str: "",
type: "",
channel_type: ""
},
isStronge:false
};
},
components: {
XImg,
LoadMore,
homeFilterList
},
beforeCreate:function(){},
created: function() {
this.tjShow = sessionStorage.getItem("tjShow"); 
this.qbShow = sessionStorage.getItem("qbShow");



if(localStorage.getItem("goodDataListArr") != ""){
this.goodDataList.data = JSON.parse(localStorage.getItem("goodDataListArr"));
console.log(this.goodDataList.data);
} else {
console.log(JSON.parse(localStorage.getItem("goodDataListArr")));
}

if (
this.tjShow == "null" ||
typeof this.tjShow == "object"
|| this.qbShow == "null" ||
typeof this.qbShow == "object"
|| this.goodDataListArr == "null" ||
typeof this.goodDataListArr == "object"




) {
this.tjShow = 0;
this.qbShow = 0;
this.goodDataListArr = [];


}





},
mounted: function() {


this.goodData(this.paramsData);


window.addEventListener("scroll", this.lodach.debounce(this.loadMore,1000));

},



methods: {

recommend: function(index) {
this.tjShow = index;
sessionStorage.setItem("tjShow", index); 

this.paramsData.param_str = "";

for(let i = 0; i < this.homeFilterList.listOne.length; i++){
if (index == i) {
this.paramsData.type = this.homeFilterList.listOne[i].param;
this.goodData(this.paramsData);
}
}
},
whole: function(index) {
this.qbShow = index;
sessionStorage.setItem("qbShow", index);

this.paramsData.param_str = "";



for(let i = 0; i < this.homeFilterList.listTwo.length; i++){
if (index == i) {
this.paramsData.channel_type = this.homeFilterList.listTwo[i].param;
this.goodData(this.paramsData);
}
}
},


selectParamStr:function(){
if(this.tjShow == 0){

this.paramsData.param_str = "1535" + Math.floor(Math.random() * 1000000) + ":" + Math.floor((this.goodDataList.data.length - 1) / 20 + 1) + ":";
} else if(this.tjShow == 1) {

if(this.qbShow == 0){
this.paramsData.param_str = "1534" + Math.floor(Math.random() * 1000000) + "_" + "1535" + Math.floor(Math.random() * 1000000) +  "_" +  Math.floor(this.goodDataList.data.length / 23 + 1);
} else if(this.qbShow == 1) {
this.paramsData.param_str = "1534" + Math.floor(Math.random() * 1000000) + "_0_" + Math.floor(this.goodDataList.data.length / 20 + 1);
} else if(this.qbShow == 2) {
this.paramsData.param_str = "1534" + Math.floor(Math.random() * 1000000);
} else if(this.qbShow == 3) {
this.paramsData.param_str = "14" + Math.floor(Math.random() * 100000000);
}

} else if(this.tjShow == 2 || this.tjShow == 3) {

if(this.qbShow == 0){
this.paramsData.param_str = "1535" + Math.floor(Math.random() * 1000000) + "_" + "1533" + Math.floor(Math.random() * 1000000) +  "_" +  Math.floor(this.goodDataList.data.length / 23 + 1);
} else if(this.qbShow == 1) {
this.paramsData.param_str = "1535" + Math.floor(Math.random() * 1000000) + "_0_" + Math.floor(this.goodDataList.data.length / 20 + 1);
} else if(this.qbShow == 2) {
this.paramsData.param_str = "152" + Math.floor(Math.random() * 10000000);
} else if(this.qbShow == 3) {
this.paramsData.param_str = "14" + Math.floor(Math.random() * 100000000);
}

} else if(this.tjShow == 4) {

if(this.qbShow == 0){
this.paramsData.param_str = "1535" + Math.floor(Math.random() * 1000000) + "_" + "1535" + Math.floor(Math.random() * 1000000) +  "_" +  Math.floor(this.goodDataList.data.length / 23 + 1);
} else if(this.qbShow == 1) {
this.paramsData.param_str = "1535" + Math.floor(Math.random() * 1000000) + "_0_" + Math.floor(this.goodDataList.data.length / 20 + 1);
} else if(this.qbShow == 2) {
this.paramsData.param_str = "153" + Math.floor(Math.random() * 10000000);
} else if(this.qbShow == 3) {
this.paramsData.param_str = "14" + Math.floor(Math.random() * 100000000);
}

}
},


goodData: function(paramsData) {
let that = this;
this.$axios
.get("/homefis/getNews", {
params: paramsData
})
.then(function(res) {
that.goodDataList = res.data;
localStorage.setItem("goodDataListArr", JSON.stringify(that.goodDataList.data)); 
})
.catch(function(err) {
console.log(err);
});
},

loadMore: function() {
let that = this;
let contentHeight = document.body.clientHeight;


let scrollTop =
window.pageYOffset ||
document.documentElement.scrollTop ||
document.body.scrollTop;
console.log(scrollTop);

let windowH = window.innerHeight;

if (scrollTop + windowH + 50 > contentHeight) {

if (this.goodDataList.data !== null) {
console.log(999);

this.selectParamStr();

if (!this.isLoadingMore) {
this.$axios
.get("/homefis/getNews", {
params: this.paramsData
})
.then(function(res) {
console.log(res.data.data);

that.goodDataList.data = that.goodDataList.data.concat(
res.data.data
);
localStorage.setItem("goodDataListArr", JSON.stringify(that.goodDataList.data)); 

that.isLoadingMore = false;
})
.catch(function(error) {
console.log(error);
});
}
this.isLoadingMore = true;
}
}
}
}
};






