import { routerList } from "../../api/data.js";

export default {
  name: "Header",
  data() {
    return {
      routerList: routerList,
      num: 0,
      backgroundImg: "backgroundImg0"
    };
  },
  created: function() {
    this.num=sessionStorage.getItem('num');   //从sessionStorage中读取状态
    if (this.num == 'null' || typeof this.num == 'object') {  //当首次进来时，从sessionStorage读取的num类型为object，刷新后，变为字符串null
      this.num = 0;
      this.backgroundImg = "backgroundImg0";
    }
    this.tabber(this.num);
  },
  methods: {
    tabber: function(index) {
      this.num = index;
      sessionStorage.setItem("num", index);  //添加到sessionStorage
      // console.log(this.num);
    }
  }
};



