import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/page/Home'
import Preferential from '@/page/Preferential'
import Find from '@/page/Find'
import Equipment from '@/page/Equipment'
import More from '@/page/More'
import SearchGood from '@/page/SearchGood'
import NotFindPage from '@/components/common/NotFindPage'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [{
    path: '/',
    name: 'Home',
    meta: {
      title: "首页"
    },
    component: Home
  },
  {
    path: '*',
    name: 'NotFindPage',
    meta: {
      title: "404"
    },
    component: NotFindPage
  },
  {
    path: '/Preferential',
    name: 'Preferential',
    meta: {
      title: "优惠"
    },
    component: Preferential
  },
  {
    path: '/Find',
    name: 'Find',
    meta: {
      title: "发现"
    },
    component: Find
  },
  {
    path: '/Equipment',
    name: 'Equipment',
    meta: {
      title: "装备"
    },
    component: Equipment,
  },
  {
    path: '/More',
    name: 'More',
    meta: {
      title: "更多"
    },
    component: More
  },
  {
    path: '/SearchGood',
    name: 'SearchGood',
    meta: {
      title: "搜索的商品"
    },
    component: SearchGood
  }]
})



