

// import { falseHotWord, falseOrder, falseBussinessbrief, falseBussinessInfo } from '../../api/data.js';

export default {

  state:{
    //bool值
    show:true,
    //计数
    count:1,
    // // 假的热搜词
    // falseHotWord,
    // // 已完成订单
    // falseOrder,
    // // 商家简略信息
    // falseBussinessbrief,
    // // 商家详细数据
    // falseBussinessInfo
  },

  getters:{
    not_show(state){ //这里的state对应着上面这个state
      return !state.show;
    }
  },

  mutations:{
    switch_dialog(state){ //这里的state对应着上面这个state
      state.show = state.show ? false : true;
      console.log(1)
      //你还可以在这里执行其他的操作改变state
    },
    conent(state){
      state.count++;
      console.log(state.count);
    }
  },

  actions:{
    switch_dialog(context){  //这里的context和我们使用的$store拥有相同的对象和方法
      context.commit('switch_dialog');
      //你还可以在这里触发其他的mutations方法
    },
    incrementAsync ({ commit }) {  //  我们可以在 action 内部执行异步操作
      setInterval(() => {
        commit('conent');
      }, 1000)
    }
  },

}



