
import HomeSwipper from "../components/home/HomeSwipper";
import SearchList from "../components/home/SearchList";
import HomeType from "../components/home/HomeType";
import HomeDiscount from "../components/home/HomeDiscount";
import HomeHotActivity from "../components/home/HomeHotActivity";
import HomeFilterList from "../components/home/HomeFilterList";
import Search from 'vux/src/components/search/index.vue'


export default {
name: "Home",
metaInfo: {
title: '首页',
meta: [
{
name: 'keywords',
content: '识货,虎扑识货,导购网,运动鞋,海淘'
},
{
name: 'description',
content: '识货网，前身为虎扑识货，是虎扑旗下专门做高性价比商品导购的网站，这里提供包括运动鞋、海淘、海内外商城等优惠信息与代购，同时网罗全网最值得买的好货。'
}
]
},
data() {
return {
results: [],
value: "",
homeShow: true,
hupuData: "",
searchValue: "",
searchList: {}
};
},
mounted: function() {

},
components: {
HomeSwipper,
HomeType,
HomeDiscount,
HomeHotActivity,
HomeFilterList,
SearchList,
Search
},
methods: {
setFocus() {
this.$refs.search.setFocus();
},
resultClick(item) {
window.alert("you click the result item: " + JSON.stringify(item));
},
getResult(val) {
this.searchValue = val;
this.searchData();
console.log("on-change", val);
},
onSubmit() {
this.$refs.search.setBlur();
console.log("on submit");
},
onFocus() {
console.log("on focus");
this.homeShow = false;
},
onCancel() {
console.log("on cancel");
this.homeShow = true;
},

searchData: function() {
let that = this;
this.$axios
.get("/search/relatedKeywords", {
params: {
keywords: that.searchValue
}
})
.then(function(response) {
that.searchList = response.data;
})
.catch(function(error) {
console.log(error);
});
}
}
};














