import Swiper from "swiper";
import { swipperListOne, swipperListTwo, prefMoregoods, hotSearch } from "../../api/data.js";
export default {
  name: "HomeSwipperTwo",
  data() {
    return {
      msg: "",
      swipperListOne: swipperListOne,
      swipperListTwo: swipperListTwo,
      prefMoregoods: prefMoregoods,
      hotSearch: hotSearch,
      // swiperOption: {
      //   paginationClickable: true,
      //   loop: true,
      //   autoplay: {
      //     delay: 1000,
      //     stopOnLastSlide: false,
      //     disableOnInteraction: false
      //   },
      //   pagination: {
      //     el: ".swiper-pagination4",
      //     type: "bullets",
      //     bulletElement: "li"
      //   },
      //   coverflow: {
      //     rotate: 30,
      //     stretch: 10,
      //     depth: 60,
      //     modifier: 2,
      //     slideShadows: true
      //   }
      // }
    };
  },
  created() {},
  mounted() {
    this.initSwiper1();
    this.initSwiper2();
    this.initSwiper3();
    this.initSwiper4();
  },
  components: {
    swipperListOne,
    swipperListTwo,
    prefMoregoods,
    hotSearch
  },
  methods: {
    initSwiper1: function() {
      var mySwiper = new Swiper(".swiper-container1", {
        //  自动播放
        autoplay: {
          delay: 1000,
          stopOnLastSlide: false,
          disableOnInteraction: false
        },
        loop: true,
        // 如果需要分页器
        pagination: {
          el: ".swiper-pagination1",
          type: "bullets",
          bulletElement: "li"
        },
        // 如果需要前进后退按钮
        navigation: {
          nextEl: ".swiper-button-next",
          prevEl: ".swiper-button-prev"
        },
        // 如果需要滚动条
        scrollbar: {
          el: '.swiper-scrollbar',
          hide: true,
        },
      });
    },
    initSwiper2: function() {
      var mySwiper = new Swiper(".swiper-container2", {
        autoplay: true, //等同于以下设置
        loop: true,
        pagination: {
          el: ".swiper-pagination2",
          type: "bullets",
          bulletElement: "li",
          dynamicBullets: true,
          dynamicMainBullets: 2
        },
        effect : 'coverflow',
      });
    },
    initSwiper3: function() {
      var mySwiper = new Swiper(".swiper-container3", {
        autoplay: 2000,
        loop: true,
        pagination: {
          el: ".swiper-pagination3",
          type: "bullets",
          bulletElement: "li"
        },
        lazy: {
          loadPrevNext: true,
        },
        zoom : true,
      });
    },
    initSwiper4: function() {
      var mySwiper = new Swiper(".swiper-container4", {
        slidesPerView : 4.5,
        freeMode : true,
        freeModeMomentumRatio : 3,
      });
    }
  }
};



